import React from "react";
import axios from "axios";
import Card from "./card";
import Formtwo from "./formtwo";
import QuizCard from "./quiz_card";

const env = require("./env");

export default class Home extends React.Component {
  constructor(props) {
    super(props);

    // console.log(this.props);
    this.state = {
      quiz_json: "",
      loader: "Loading...",
      instruction: false,
      displayQuestions: false,
      closeModal: false
    };
  }

  async componentDidMount() {


    // setTimeout (() => {
    // console.log(JSON.stringify(env.quiz_json))
    if (localStorage.getItem("quiz_json") == null) {
      await axios.get(`http://ec2-35-170-185-221.compute-1.amazonaws.com:5000/?file=quiz_json.json`)
        .then(res => {
          localStorage.setItem("quiz_json", JSON.stringify(res.data));
          this.setState({
            quiz_json: JSON.stringify(res.dada)
          });
        })
    } else {
      console.log(JSON.parse(localStorage.getItem("quiz_json")))
      this.setState({
        quiz_json: JSON.parse(localStorage.getItem("quiz_json"))
      });
    }
    this.setState({ loader: "", instruction: true });

    // }, 500);
    console.log("here", JSON.parse(localStorage.getItem("quiz_json")));

  }

  ok = () => {
    this.setState({ displayQuestions: true });
    this.setState({ instruction: false });
    this.setState({ closeModal: true });
  };

  closeModal = () => {
    console.log("here")
    this.setState({ closeModal: false });
  }


  render() {
    return (
      <div>
        <div className="text-center download-div">
          <h3> {this.state.loader} </h3>
        </div>
        {this.state.instruction && (
          <div>
            <h5>general instruction</h5>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500s, when an unknown printer took a galley of
              type and scrambled it to make a type specimen book. It has
              survived not only five centuries, but also the leap into
              electronic typesetting, remaining essentially unchanged. It was
              popularised in the 1960s with the release of Letraset sheets
              containing Lorem Ipsum passages, and more recently with desktop
              publishing software like Aldus PageMaker including versions of
              Lorem Ipsum.
              </p>
            <button type="button" className="btn btn-primary" onClick={this.ok} data-toggle="modal" data-target="#exampleModal">
              Start Test!
          </button>
          </div>
        )}
        {this.state.displayQuestions && (
          <QuizCard data={JSON.parse(localStorage.getItem("quiz_json"))} />
        )}

        {/* <!-- Modal --> */}
        {this.state.closeModal && (
          <div className="modal fade" >
            {/* <div className="modal-dialog" role="document"> */}
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Modal title</h5>
              </div>
              <div className="modal-body">
                <p>
                  <br />
                  Swipe Up or Down  to View More Questions
                  </p>
              </div>
              <div className="modal-footer">
                <button onClick={this.closeModal} type="button" className="btn btn-secondary"  >OK, GOT IT</button>
              </div>
            </div>
          </div>)}
      </div>
    );
  }
}