import React from "react";
import Header from "./header"
const parse = require("html-react-parser");

export default class QuizCard extends React.Component {

  state = {
    htmlData: "",
    selected: null,
    options: "",
    wholeQuestion: [],
    ii: []
  };
  updateAnswer = (qn_id, option_id, q_answer) => {
    document.getElementById(option_id).checked = true
    console.log(qn_id, option_id)
    // console.log(this.state.wholeQuestion)
    this.state.wholeQuestion.map((data) => {
      if (data.id == qn_id) {
        data.options.map((opt) => {
          if (opt.id == option_id) {
            console.log(qn_id, option_id, "here")
            let dd = JSON.parse(localStorage.getItem('quiz_json'))
            dd.questions[qn_id].q_status = option_id
            var marvelHeroes = dd.attended_questions.filter(function (hero) {
              return hero.question_id == qn_id
            });
            console.log(marvelHeroes, "marvelHeroes", marvelHeroes[0])
            if (marvelHeroes.length == 0 && marvelHeroes[0] == undefined) {
              dd.attended_questions.push({ question_id: qn_id, checked_option_id: option_id, correct_answer_id: q_answer })

            } else {
              dd.attended_questions.map((element, index) => {
                console.log(element.question_id, "element.qiestion_id", qn_id, "qn_id")

                if (element.question_id == qn_id) {
                  console.log(dd, "dd1", index)
                  dd.attended_questions[index].checked_option_id = option_id
                  console.log(dd, "dd2")
                }

              });
            }
            localStorage.setItem('quiz_json', JSON.stringify(dd))
            console.log(dd)

            // if(dd.questions[qn_id] == )
          }
        })
      }
    })
  }
  fff(a, b) {
    console.log(a, b)
    if (a == b) {
      setTimeout(() => {
        document.getElementById(b).checked = true
      }, 500)
    }
  }

  componentDidMount() {
    let eachQuestion = [];
    for (let a in this.props.data.questions) {
      eachQuestion.push(this.props.data.questions[a]);
    }
    this.setState({ wholeQuestion: eachQuestion })
    const arrayValues = eachQuestion.map((val, index) => {
      return (
        <div className="card" key={index}>
          <div className="card-body">
            <p className="card-title">Single Choice</p>
            <div className="card-text">{parse(val.q_question[1])}</div>
            {val.options[0] &&
              <div className="radio" >
                {/* <p>{val.q_status}{val.options[0].id}</p> */}
                <label className="option"><input type="radio" id={val.options[0].id} onLoad={this.fff(val.q_status, val.options[0].id)} onClick={() => this.updateAnswer(val.id, val.options[0].id, val.q_answer)} name={val.id} />{parse(val.options[0].qo_options[1])}</label>
              </div>
            }
            {val.options[1] &&
              <div className="radio">
                <label className="option"><input type="radio" id={val.options[1].id} onLoad={this.fff(val.q_status, val.options[1].id)} onClick={() => this.updateAnswer(val.id, val.options[1].id, val.q_answer)} name={val.id} />{parse(val.options[1].qo_options[1])}</label>
              </div>
            }
            {val.options[2] &&
              <div className="radio">
                <label className="option"><input type="radio" id={val.options[2].id} onLoad={this.fff(val.q_status, val.options[2].id)} onClick={() => this.updateAnswer(val.id, val.options[2].id, val.q_answer)} name={val.id} />{parse(val.options[2].qo_options[1])}</label>
              </div>
            }
            {val.options[3] &&
              <div className="radio">
                <label className="option"><input type="radio" id={val.options[3].id} onLoad={this.fff(val.q_status, val.options[3].id)} onClick={() => this.updateAnswer(val.id, val.options[3].id, val.q_answer)} name={val.id} />{parse(val.options[3].qo_options[1])}</label>
              </div>
            }
            {val.options[4] &&
              <div className="radio" >
                <label className="option"><input type="radio" id={val.options[4].id} onLoad={this.fff(val.q_status, val.options[4].id)} onClick={() => this.updateAnswer(val.id, val.options[4].id, val.q_answer)} name={val.id} />{parse(val.options[4].qo_options[1])}</label>
              </div>
            }
          </div>
        </div>
      )
    });
    this.setState({ htmlData: arrayValues });
  }

  render() {
    return (
      <div className={"temp" + " container"}>
        <div className="row">
          <div className="col-md-4">
          </div>
          <div className="col-md-4">
            <Header />
            <div>{this.state.htmlData}</div>
          </div>
          <div className="col-md-4">
          </div>
        </div>
      </div>
    );
  }
}
