import React from "react";
import { NavLink } from "react-router-dom"
const env = require('./env')

export default class Header extends React.Component {
    constructor(props) {
        super(props);

        // console.log(this.props);
        this.state = {
            closeModal: false,
            correctAnswer: '',
            totalQuestions: '',
            answerd: ''
        };
    }
    componentDidMount() {

    }
    handletimer = () => {
        var countDownDate = new Date("Jan 5, 2021 15:37:25").getTime();
        var x = setInterval(function () {
            var now = new Date().getTime();
            var distance = countDownDate - now;
            var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = Math.floor((distance % (1000 * 60)) / 1000);
            document.getElementById("demo").innerHTML = minutes + "m " + seconds + "s ";
            if (distance < 0) {
                clearInterval(x);
                document.getElementById("demo").innerHTML = "EXPIRED";
            }
        }, 1000);
        return (
            <span>Time:
            <span id="demo"></span>
            </span>
        )
    }
    close = () => {
        this.setState({ closeModal: false });
        // this.render()
    }
    closeModal = () => {
        let data = JSON.parse(localStorage.getItem("quiz_json"));
        var marvelHeroes = data.attended_questions.filter((res) => {
            return res.checked_option_id == res.correct_answer_id
            // var marvelHeroes = dd.attended_questions.filter(function (hero) {
            //     return hero.question_id == qn_id
            //   });
        })
        console.log(marvelHeroes.length, "marvelHeroes")
        this.setState({ correctAnswer: marvelHeroes.length, totalQuestions: 100, answerd: data.attended_questions.length })
        // console.log("here", this.state)
        this.setState({ closeModal: true });
    }
    render() {
        return (
            <div>
                <h2 style={{ backgroundColor: "green" }}>JEE 2020 Revision: Thermal Properties<br />
                    of Matter Quiz<br />
                    8 Questions   | 15 Minutes
                </h2>
                <div style={{ display: "flex", justifyContent: "space-between" }}>

                    <div> {this.handletimer()}</div>
                    <div > 1/30</div>
                    <div>
                        <button onClick={this.closeModal} type="button" value="submit" data-toggle="modal" className="btn btn-primary" >Submit</button>
                    </div>
                </div>
                {this.state.closeModal && (
                    <div className="modal fade" >
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" >Submit Test</h5>
                                    {/* <button type="button" className="close" >
                                    <span aria-hidden="true">&times;</span>
                                </button> */}
                                </div>
                                <div className="modal-body">
                                    <p>Total Questions : 100</p>
                                    <p>Correct Answeres : {this.state.correctAnswer}</p>
                                    <p>Answered : {this.state.answerd}</p>
                                    <p>UnAnswered : {100 - this.state.answerd}</p>
                                </div>
                                <div className="modal-footer">
                                    {/* <button type="button" className="btn btn-secondary" onClick={this.close}>Cancel</button> */}
                                    <button type="button" className="btn btn-primary" onClick={this.close}>Submit</button>
                                </div>
                            </div>
                        </div>
                    </div>)}
            </div>
        )
    }
}